body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
		"Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
	text-align: center;
	background-image: url("https://equarz.com/galerie/objekt_manuell/8829-equarz-bianco-stardust-quarzwerkstoff-arbeitsplatte.jpg");
}

body {
	font-family: "Righteous", cursive;
	color: #0f0e0e;
	line-height: 1.7;
	font-size: 19px;
	background-color: #222222;
	background-image: url("https://apod.nasa.gov/apod/image/1908/MoonSaturn_Patonai_960.jpg");
}

/_ navbar ---------- _/ .navbar {
	background-color: rgba(22, 22, 22, 0.8);
	display: flex;
	justify-content: center;
}

.link {
	color: #efefef;
	display: inline-block;
	padding: 3px 20px;
	font-size: 16px;
	text-decoration: none;
	transition: 0.3s ease all;
}

.link:hover {
	color: #f59494;
	transform: scale(1.1);
}

/_ nasa photo ---------- _/ .nasa-photo {
	display: flex;
	padding: 40px;
	margin: 0 auto;
	max-height: 100%;
}

.nasa-photo img {
	width: 50%;
	margin-right: 50px;
	object-fit: contain;
	max-height: 80vh;
}

.nasa-photo h1 {
	margin-top: 0;
	margin-bottom: 0;
}

.nasa-photo .date {
	margin-top: 0;
	margin-bottom: 30px;
}

.nasa-photo .explanation {
	color: #bbb;
}

@media (max-width: 1280px) {
	.nasa-photo {
		display: block;
	}

	.nasa-photo img {
		width: 100%;
		margin-right: 0;
		margin-bottom: 30px;
	}
}

.home {
	display: flex;
	height: 100vh;
	align-items: center;
	justify-content: center;
}

.home-link {
	font-size: 40px;
	background: #f59494;
	display: inline-block;
	padding: 40px 80px;
	color: #fff;
	border-radius: 50%;
	text-decoration: none;
	transition: 0.3s ease all;
	text-transform: uppercase;
}

.home-link:hover {
	border-radius: 5px;
	background: #e77171;
}

.body {
	background-color: black;
}

.navigation {
	position: relative;

	width: 170px;
	left: 15px;
	background-size: contain;
}

.primary {
	background-color: red;
}
.searchbar {
	background-color: black;
}

